import React, { Component } from 'react';
import './NotFound.css';

class NotFound extends Component {
  render() {
    return (
      <div className="NotFound">
        <h2>Error: 404</h2>
        <h1>Page Not Found</h1>
      </div>
    )
  }
}

export default NotFound;



